.block-info {
  overflow: hidden;
  // Width:243px;
  height: 50px;
  @media screen and(min-width:768) {
    // Width:265px;
    height: 100px;
  }
  @media screen and(min-width:1280) {
    // Width:450px;
    height: 66px;
  }
  .modal-text {
    text-align: center;
    animation: text 15s infinite linear;
    animation-direction: alternate;

    white-space: pre-wrap;
  }
  @keyframes text {
    0% {
      transform: translate(0, 0);
    }

    100% {
      transform: translate(0, -160%);
    }
  }
}

.backdrop {
  position: fixed;
  z-index: 99;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-color: rgba(32, 13, 29, 0.6);
  backdrop-filter: blur(3px);
}

.is-open {
  opacity: 1;
  pointer-events: initial;
}

.modal-js {
  top: 0;
  position: fixed;
  position: absolute;
  z-index: 2;
  overflow: hidden;
  -webkit-overflow-scrolling: touch;
  padding: 50px 15px 70px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: var(--modal-bg-color);
  border-radius: 150px 0;
  color: black;
  text-align: center;
  @media screen and(min-width: 320px) {
    width: 320px;
  }
  @media screen and(min-width: 480px) {
    width: 430px;
  }
  @media screen and(min-width: 768px) {
    display: flex;
    flex-flow: column nowrap;
    justify-content: center;
    align-items: center;
    border-radius: 300px 0;
    width: 595px;
    padding: 30px 50px 55px;
  }
  @media screen and(min-width: 1280px) {
    width: 740px;
    // min-height: 70%;
    padding: 62px 60px;
  }
}

.btn-modal-close {
  border: none;
  background-color: var(--modal-bg-color);
  color: var(--modal-blue-color);
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 25px;
  right: 25px;
  width: 17px;
  height: 17px;
  font-size: 24px;
  @media screen and(min-width: 320px) {
    width: 20px;
    height: 20px;
  }
  @media screen and(min-width: 1280px) {
    font-size: 32px;
    width: 25px;
    height: 25px;
  }
}

.wrapper-image-small {
  margin: 0 auto 20px;
  width: 100px;
  height: 100px;
  border-radius: 50%;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  @media screen and(min-width: 768px) {
    width: 70px;
    height: 70px;
  }
  @media screen and(min-width: 1280px) {
    width: 132px;
    height: 132px;
    margin: 0 auto 40px;
  }
}
.modal-image-small {
  height: 100%;
  width: auto;
}

.modal-description {
  margin-top: 10px;
  @media screen and(min-width: 320px) {
    text-align: center;
  }
  @media screen and(min-width: 768px) {
    display: flex;
    margin-top: 45px;
  }
  @media screen and(min-width: 1280px) {
    margin-top: 0px;
  }
}

.wrapper-image-big {
  display: none;
  @media screen and(min-width: 768px) {
    width: 260px;
    height: 415px;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 30px;
  }
  @media screen and(min-width: 1280px) {
    width: 360px;
    height: 440px;
    margin-right: 60px;
  }
}

.modal-image-big {
  display: none;
  @media screen and(min-width: 768px) {
    height: 100%;
    width: auto;
    display: block;
  }
}

.text-wrapper {
  text-align: center;
  @media screen and(min-width: 768px) {
    text-align: start;
    width: calc(100% / 2);
  }
}

.title {
  color: var(--accent-color);
  font-weight: 600;
  font-size: 20px;
  line-height: 1.2;
  &:not(:first-child) {
    margin-top: 10px;
  }
  // margin-top: 10px;
  @media screen and(min-width: 1200px) {
    font-size: 24px;
    &:not(:first-child) {
      margin-top: 20px;
    }
  }
}

.modal-text {
  font-weight: normal;
  font-size: 16px;
  margin-top: 5px;
  line-height: 1.25;
  &.buy {
    margin-top: 20px;
  }
  @media screen and(min-width: 1200px) {
    font-size: 18px;
    line-height: 1.2;
  }
}

.modal-btn {
  display: inline-block;
  height: 40px;
  width: 145px;
  margin: 10px auto 0;
  background-color: var(--modal-blue-color);
  color: white;
  border: none;
  border-radius: 5px;
  font-weight: 500;
  font-size: 16px;
  line-height: 1.25;
  text-align: center;
  padding: 10px 20px;

  @media screen and(min-width: 1200px) {
    margin-top: 20px;
  }
}

.modal-btn-more {
  display: inline-block;
  height: 40px;
  width: 235px;
  margin: 25px auto 0;
  color: var(--modal-blue-color);
  border: 1px solid var(--modal-blue-color);
  border-radius: 5px;
  font-weight: 500;
  font-size: 16px;
  line-height: 1.25;
  text-align: center;
  padding: 10px 1px;
  @media screen and(min-width: 768px) {
    width: 255px;
    margin-top: 45px;
  }
}
